<template>
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
        <div>
            <i class="el-icon-search"></i>
            <span></span>
            <el-button
            style="float:right"
            type="primary"
            @click="handleSearchList()"
            size="small">
            
            </el-button>
            <el-button
            style="float:right;margin-right: 15px"
            @click="handleResetSearch()"
            size="small">
            
            </el-button>
        </div>
        <div style="margin-top: 15px">
          <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
            <el-form-item label="">
                <el-input v-model="listQuery.orderNumber" style="width: 203px" class="input-width" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="listQuery.consigneeName" style="width: 203px" class="input-width" placeholder="/"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="listQuery.orderStatus" style="width: 203px" class="input-width" placeholder="" clearable>
                    <el-option v-for="item in statusOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                  style="width: 363px"
                  v-model="listQuery.selectTime"
                  type="datetimerange"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder="">
                </el-date-picker>
            </el-form-item>
          </el-form>
        </div>
    </el-card>
    <!-- show-overflow-tooltip  tooltip!!!!!!!!!!!!!!!!!!!!!!!!!-->
    <div class="table-container">
      <el-table ref="orderTable"    
          highlight-current-row
          show-overflow-tooltip
        
          height="250"
          :data="list"
          style="width: 100%;"
          @selection-change="handleSelectionChange"
          v-loading="listLoading" border>
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column label="" width="80" align="center">
          <template slot-scope="scope">{{scope.row.orderId}}</template>
        </el-table-column>
        <el-table-column label="" width="130" align="center">
          <template slot-scope="scope">{{scope.row.consigneeName}}</template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">{{scope.row.orderNumber}}</template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">{{scope.row.createTime | formatCreateTime}}</template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">{{scope.row.paymentMoney}}</template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">{{scope.row.paymentMode | formatPayType}}</template>
        </el-table-column>
        <!-- <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">{{scope.row.sourceType | formatSourceType}}</template>
        </el-table-column> -->
        <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">{{scope.row.orderStatus | formatStatus}}</template>
        </el-table-column>
        <el-table-column  label="" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleViewOrder(scope.$index, scope.row)"
            ></el-button>
            <el-button
              size="mini"
              @click="handleCloseOrder(scope.$index, scope.row)"
              v-show="scope.row.status===0"></el-button>
            <el-button
              size="mini"
              @click="handleDeliveryOrder(scope.$index, scope.row)"
              v-show="scope.row.status===1"></el-button>
            <el-button
              size="mini"
              @click="handleViewLogistics(scope.$index, scope.row)"
              v-show="scope.row.status===2||scope.row.status===3"></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDeleteOrder(scope.$index, scope.row)"
              v-show="scope.row.status===4"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="batch-operate-container">
      <el-select
        size="small"
        v-model="operateType" placeholder="">
        <el-option
          v-for="item in operateOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button
        style="margin-left: 20px"
        class="search-button"
        @click="handleBatchOperate()"
        type="primary"
        size="small">
        
      </el-button>
    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
    <!-- <el-dialog
      title=""
      :visible.sync="closeOrder.dialogVisible" width="30%">
      <span style="vertical-align: top"></span>
      <el-input
        style="width: 80%"
        type="textarea"
        :rows="5"
        placeholder=""
        v-model="closeOrder.content">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeOrder.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="handleCloseOrderConfirm"> </el-button>
      </span>
    </el-dialog>
    <logistics-dialog v-model="logisticsDialogVisible"></logistics-dialog> -->

  </div> 
</template>
<script>
import {fetchList} from '@/api/orders'
const defaultListQuery = {
    pageNum: 1,
    pageSize: 10,
    orderNumber: null,
    consigneeName:null,
    orderStatus:null,
    selectTime:null,
  };
export default {
    data(){
        return{
          listQuery: Object.assign({}, defaultListQuery),
          //listLoading: true,
          list: null,
          total: null,
          statusOptions:[
            {
              label:'Pending payment',//
              value:'0',
            },
            {
              label:'To be shipped',//
              value:'1',
            },
            {
              label:'Shipped',//
              value:'2',
            },
            {
              label:'Completed',//
              value:'3',
            },
            {
              label:'Closed',//
              value:'4',
            },
            {
              label:'Returning',//
              value:'5',
            },
            {
              label:'refunding',//
              value:'6',
            }
          ],
          list:[
            {
              orderId:'1231',
              orderNumber:'2323434',
              consigneeName:'',
              createTime:'2019-2-2',
              paymentMoney:'2000',
              paymentMode:'',
              orderStatus:'',

            },
            {
              orderId:'1231',
              orderNumber:'2323434',
              consigneeName:'',
              createTime:'2019-2-2',
              paymentMoney:'2000',
              paymentMode:'',
              orderStatus:'',
            },
            {
              orderId:'1231',
              orderNumber:'2323434',
              consigneeName:'',
              createTime:'2019-2-2',
              paymentMoney:'2000',
              paymentMode:'',
              orderStatus:'',
            }
          ],

          pickerOptions2: {
          shortcuts: [{
              text: '',
              onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
              }
          }, {
              text: '',
              onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
              }
          }, {
              text: '',
              onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
              }
          }]
          },
          selectTime:'',
            
        }
    },
    methods:{

      //
      getSearchList(){
          this.listLoading=true;
          //this.listQuery
          fetchList(this.listQuery).then(response => {
            this.listLoading = false;
            this.list = response.data.list;
            this.total = response.data.total;
          });
      },

      //
      handleSearchList(val){
          this.listQuery.pageNum = 1;
          this.listQuery.pageSize = val;
          this.getSearchList();
      },

      //
      handleResetSearch(){
        this.listQuery = Object.assign({}, defaultListQuery);
      },

        handleBatchOperate(){},
        handleViewOrder(){},
        handleCloseOrder(){},
        handleSelectionChange(){},
        handleDeleteOrder(){},
        handleViewLogistics(){},
        handleDeliveryOrder(){},
        handleCloseOrderConfirm(){},
    }
}
</script>

<style scoped>
  .pagination-container{
    float:right;
  }
  .batch-operate-container{
    display: inline-block;
  }
  
</style>



